body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 10% 40%;
  background: #000;
  color:#fff;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
 
}

.status {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
}

.square {
  background: #000;
  border: 3px solid #eee;
  float: left;
  font-size: 30px;
  color: #fff;
  font-weight: bold;
  line-height: 60px;
  height: 60px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 60px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
  
}
